import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Nlp from '../views/NLP.vue'
import TinyML from '../views/TINYML.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '*',
    name: 'Home',
    component: Home
  },
  {
    path: '/nlp-identifying-antisemitic-tweets',
    name: 'Nlp',
    component:Nlp
  },
  {
    path: '/tinyml-audio-recognition',
    name: 'TinyML',
    component: TinyML
  },
]

const router = new VueRouter({
  routes,
  mode:'history'
})

export default router
