, a well-founded background in antisemitic studies was needed. The goal of the Hackathon, on the other hand, was to classify tweets programmatically as either antisemitic (1) or non-antisemitic (0). For the hackathon, a bunch of labeled data was given in a file. Because I was the only one with programming skills in the team, I was happy to think about a solution for the hackathon, even though I had no experience with NLP at all"}}),_c('Desktop-Text',{attrs:{"text":"4 weeks later","size":"18px","weight":"550","align":"left"}}),_vm._m(0),_c('Desktop-Text',{attrs:{"text":"\"The first prize of $500 went to Jan Fiedler, Sydney Grad, Sophie Mariassy and Victor Tschiskale, students from Canada and Germany.\"","size":"25px","weight":"100","align":"center"}}),_vm._m(1),_c('Desktop-Text',{attrs:{"text":"We actually won!"}}),_c('Desktop-Text',{attrs:{"text":"The rest of my team annotated a crazy amount of tweets properly, which won us the datathon. For the hackathon, we also took first place because of my machine learning model, which achieved an F1 score of 0.90 on the final validation data."}}),_c('Desktop-Text',{attrs:{"text":"A basic NLP approach","size":"20px","weight":"550","align":"left"}}),_c('Desktop-Text',{attrs:{"text":"In the following article, I want to reveal you a really basic approach for classifying text in Python. This approach is based on an article from Rebecca Vickery on","linktext":"medium.","link":"https://medium.com/vickdata/detecting-hate-speech-in-tweets-natural-language-processing-in-python-for-beginners-4e591952223"}}),_c('Desktop-Text',{attrs:{"text":"Thank you so much for sharing!"}}),_c('Desktop-Text',{attrs:{"text":"The Data","size":"18px","weight":"550","align":"left"}}),_c('Desktop-Text',{attrs:{"text":"As I said, there was a data set given in a JSON file, consisting of labeled tweets either as antisemitic (1) or non-antisemitic (0). I converted the given JSON into a pandas data frame to keep the data science manner "}}),_c('DesktopTable',{attrs:{"items":_vm.table0,"caption":"a part of the given data set"}}),_c('Desktop-Text',{attrs:{"text":"Cleaning the Data","size":"18px","weight":"550","align":"left"}}),_c('Desktop-Text',{attrs:{"text":"In order to make any machine algorithm work well, I had to pre-process the given text data. As you can see, tweets contain characters like hashtags or emojis, which wont be meaningful to a machine learning algorithm."}}),_c('Desktop-Text',{attrs:{"text":"I am using the re library in Python for removing these characters."}}),_c('Desktop-Code',{attrs:{"content":" import re\n\n      def f_clean_data(df,field):\n          df[field]=df[field].str.lower()\n          df[field] = df[field].apply(lambda elem: \n          re.sub(r\"(@[A-Za-z0-9]+)|([^0-9A-Za-z \\t])|(\\w+:\\/\\/\\S+)|^rt|http.+?\",\"\", elem))\n          return df"}}),_c('Desktop-Text',{attrs:{"text":"This function also converts every word in lower case, which is nice because the following algorithm will count the frequency of words. Without this processing, a lower and upper version of the same word would count differently, even if the meaning is the same.\nLets have a look at the cleaned data."}}),_c('DesktopTable',{attrs:{"items":_vm.table1,"caption":"a part of the given data set - cleaned"}}),_c('Desktop-Text',{attrs:{"text":"This is looking a lot cleaner. After some research, I thought another important step of cleaning my tweets is needed: removing stop words and lemmatization. Stop words are the most common and basic words in a language like to, the or and. Those words had to be removed because they cant give a hint about the intention of a tweet. Lemmatization is a linguistic process of affiliating a word to its very basic form (lemma). For example runs, ran and running are forms of the same lemma: run. Like in the lowercase processing, this is really important to identify equal words even if they are in different forms."}}),_c('Desktop-Text',{attrs:{"text":"I implemented this by using spacy, a really strong library for NLP. I am basically splitting up my tweet as a text in separate words (tokens). If the token is a stop word, I dont want to append it to my cleaned text. If it is not a stop word, I want to apply the lemmatization and append it to my cleaned text."}}),_c('Desktop-Code',{attrs:{"content":" import spacy\n\n      nlp=spacy.load(\"en_core_web_sm\")\n\n      def remove_stop_apply_lemma_for_string(text):\n          cleaned_text=\"\"\n          tokens=nlp(text)\n          for token in tokens:\n              if not token.is_stop:\n                  cleaned_text=cleaned_text+\" \"+token.lemma_\n    \n          return cleaned_text"}}),_c('Desktop-Text',{attrs:{"text":"At this point in pre-processing the tweets, it is getting harder to understand the text as a human being. On the other hand, this will improve the performance of our algorithm in the end."}}),_c('DesktopTable',{attrs:{"items":_vm.table2,"caption":"a part of the given data set  applied lemmatization and removed stop words"}}),_c('Desktop-Text',{attrs:{"text":"Balancing the Classes","size":"18px","weight":"550","align":"left"}}),_c('Desktop-Text',{attrs:{"text":"After having the data cleaned I realized, that the number of non-antisemitic tweets predominated the antisemitic tweets. If the machine learning model is only being fed with tweets, which are labeled as 0, it is more likely, that it will predict new tweets to 0 in the end.\nHaving the classes balanced is an important requirement to proceed in this stage."}}),_c('Desktop-Text',{attrs:{"text":"It is one approach, to either upsample or downsample the minority/majority of a class. At this point, it was really about trying which method scores better results. I went for upsampling the minority, where samples of the minority are being used multiple times until the class is the same size as the majority. For this implementation, I used the functions of the sklearn library."}}),_c('Desktop-Code',{attrs:{"content":" from sklearn.utils import resampl