<template>
  <div class="home">
    <div class="my_header">
      <DesktopHeader id="DesktopHeader"></DesktopHeader>
      <MobileHeader id="MobileHeader"></MobileHeader>
    </div>
    <div id="desktop-body">
      <DesktopEntry
        title="Audio Recognition with Embedded Machine Learning"
        subtitle="How do Harry Potter, TinyML and an Arduino match each other, you may wonder? 
      Follow along and see how to become a wizard using embedded machine learning."
        author_date="Jan Fiedler  Aug 02, 2020"
      ></DesktopEntry>
      <Desktop-Text
        text="Recently I got aware of something called tiny machine learning or TinyML. 
      TinyML is basically the latest technology for embedded systems, where deep learning and tiny devices are combined to create something very potential. 
      Traditional machine learning often runs on big powered hardware somewhere in the cloud and therefore requires a good amount of resources."
      >
      </Desktop-Text>
      <Desktop-Text
        text="With TinyML it is now possible to run machine learning models on the edge with devices like the Arduino nano 33 ble, which fits literally everywhere and only consumes about 20 mA."
      >
      </Desktop-Text>
      <Desktop-Picture
        caption="The Arduino Nano 33 BLE has an integrated 3 axis acceleration sensor, a microphone as well as an Bluetooth interface"
        width="300px"
      >
      </Desktop-Picture>
      <Desktop-Text
        text='The most common application of this technology is the recognition of wake words like Hey "Siri" or "Alexa" by known smart devices. 
      If those devices are in standby mode, a low powered chip is constantly waiting for you to say the wake word to power up the main CPU of the device.'
      >
      </Desktop-Text>
      <Desktop-Text
        text="In December 2019, Peter Waren and Daniel Situnayake made this public by releasing a book, which explains machine learning with TensorFlow Lite on Arduino and Ultra-Low-Power Micro-Controllers. Since then, things got even easier with the launch of Edge Impulse: TinyML as a service to enable machine learning for all developers. It provides the full TinyML Pipeline, starting by collecting data, building a model and deploying it to the device. As a result, the implementation of motion- and sound recognition or finding anomalies in sensor data became more accessible"
      >
      </Desktop-Text>
      <Desktop-Text
        text="So I decided to build a voice recognition model with edge impulse!"
        size="25px"
        weight="650"
        align="center"
      >
      </Desktop-Text>
      <DesktopPictureSideText
        :picture="lumos"
        caption="Because I am quite a big fan of Harry Potter, I thought it would be fun to build my own magic wand. If you are not into Harry Potter, let me tell you, that Lumos is the spell for creating light. On the other hand Nox makes the light disappear."
        width="250px"
      >
      </DesktopPictureSideText>
      <Desktop-Text
        text="Data acquisition"
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="To get started with every machine learning project, you got to have some data in-store to train your model later on. Because I could not find datasets of people saying Lumos or Nox online, I had to sample the data myself. Thanks to Edge Impulse, this was not really a problem. After installing the Edge Impulse firmware on my Arduino and some further installations on my desktop PC I was able to record data and save it directly to my Edge Impulse project."
      >
      </Desktop-Text>
      <Desktop-Picture
        caption="Data acquisition in Edge Impulse"
        width="800px"
        :picture="data_acq"
      >
      </Desktop-Picture>
      <Desktop-Text
        text="So I took some time and recorded myself 12 minutes of data, which can be divided into 3 labels. Because I want my model to recognize me saying a spell name, I recorded myself ~4 minutes of saying Lumos as well asNox. Because the model also needs to recognize, what silence or noise looks like, I had to record ~4 min of noise as well."
      >
      </Desktop-Text>
      <Desktop-Text
        text="Building the model"
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="After collecting a sufficient amount of audio data, it was time to build our machine learning model. You can do this by adding pre-defined blocks to your impulse-pipeline:"
      >
      </Desktop-Text>
      <Desktop-Picture
        caption="Impulse Design"
        width="1000px"
        :picture="impulse"
      >
      </Desktop-Picture>
      <Desktop-Text
        text="At first, I had to configure my time-series data, which defines the window size and the window increase. The window size is simply a frame, where your recorded audio data is organized in. This setting also defines the time, the microphone of the Arduino is recording after the deployment. The window increase is an offset, which splits you recorded audio into even more data. The lower the window increase, the more data you are going to receive for training. 200 milliseconds worked pretty well for me."
      >
      </Desktop-Text>
      <Desktop-Text
        text="After cutting the data in parts, these window sized samples are going to be processed by an MFCC block. This is a well known processing for generating features from audio using Mel Frequency Cepstral Coefficients. The following picture shows the output of a recorded sample:"
      >
      </Desktop-Text>
      <Desktop-Picture
        caption="DSP Result of noise"
        width="800px"
        :picture="mfcc"
      >
      </Desktop-Picture>
      <Desktop-Text
        text="This MFCC processing converts the raw audio signal in a frequency bucket matrix, where features can be extracted for learning our model."
      >
      </Desktop-Text>
      <Desktop-Text
        text="Edge Impulse offers a pretty cool view to observe all processed features of your recorded data. Mine looked like that:"
      >
      </Desktop-Text>
      <Desktop-Picture
        caption="Feature explorer view - lumos, nox and noise"
        width="800px"
        :picture="features"
      >
      </Desktop-Picture>
      <Desktop-Text
        text="One can see, that the processed features of each label are more or less separated, which is quite promising for our model."
      >
      </Desktop-Text>
      <Desktop-Text
        text="At last, I am adding a Nearest Neighbour classifier for my learning block in the impulse pipeline by changing some training parameters, I am pretty much done building my machine learning model. It is time to let the model train!"
      >
      </Desktop-Text>
      <Desktop-Text
        text="Training the model"
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Picture
        caption="Training performance"
        width="1000px"
        :picture="result"
      >
      </Desktop-Picture>
      <Desktop-Text
        text="Sweet! Those results are looking pretty good. I was aware of the effect of overfitting my data but some live classification assured me, that the model is working not too shabby. In this article, I am not going over the process of live classification and retraining your model. I just want to mention, that this also works quite well."
      >
      </Desktop-Text>
      <Desktop-Text text="Deployment" size="18px" weight="550" align="left">
      </Desktop-Text>
      <Desktop-Text
        text="There are several possibilities to deploy your generated model to your device. One of them is creating a Arduino library in Edge Impulse and importing it in the Arduino IDE. This library comes with examples for testing your model, which makes it extremely easy to adjust the code for your specific use case."
      >
      </Desktop-Text>
      <Desktop-Text text="Result" size="18px" weight="550" align="left">
      </Desktop-Text>
      <Desktop-Text
        text="Like I said in the beginning, I wanted to light up something, when saying Lumos or shut the light, when saying Nox. I also wanted the microphone to only record, while a button is pressed. This is why I added some code and did some soldering on my Arduino. I really wanted this project to be fully embedded, so a battery block is attached at the back of my wand. So here it is:"
      >
      </Desktop-Text>
      <Desktop-Picture caption="poor mans wand" width="400px" :picture="wand">
      </Desktop-Picture>
      <Desktop-Text
        text="Okay, it may not be as pretty and powerful as Dumbledores Elderwand, but it is quite functional. "
      >
      </Desktop-Text>
      <Desktop-Text text="Let me show you:"> </Desktop-Text>
      <iframe id="youtube" width="700" height="400" src="https://www.youtube.com/embed/7hcYYn1x_Hs" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      <Desktop-Text text="I am sure, they are some more easy ways to detect words or speech in general, but this project was just about trying out TinyML. Just think about how easy and cheap you could realize predictive maintenance with these tools. I am really excited, what people are going to build with this technology."> </Desktop-Text>
      <div class="line">
        <hr>
      </div>
      <Desktop-Text
        text="Thank you for coming by "
        :align="center"
      >
      </Desktop-Text>
      <Desktop-Footer>
      </Desktop-Footer>
    </div>
