var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"home"},[_c('div',{staticClass:"my_header"},[_c('DesktopHeader',{attrs:{"id":"DesktopHeader"}}),_c('MobileHeader',{attrs:{"id":"MobileHeader"}})],1),_c('div',{attrs:{"id":"desktop-body"}},[_c('DesktopEntry',{attrs:{"title":"Audio Recognition with Embedded Machine Learning","subtitle":"How do Harry Potter, TinyML and an Arduino match each other, you may wonder? \n    Follow along and see how to become a wizard using embedded machine learning.","author_date":"Jan Fiedler  Aug 02, 2020"}}),_c('Desktop-Text',{attrs:{"text":"Recently I got aware of something called tiny machine learning or TinyML. \n    TinyML is basically the latest technology for embedded systems, where deep learning and tiny devices are combined to create something very potential. \n    Traditional machine learning often runs on big powered hardware somewhere in the cloud and therefore requires a good amount of resources."}}),_c('Desktop-Text',{attrs:{"text":"With TinyML it is now possible to run machine learning models on the edge with devices like the Arduino nano 33 ble, which fits literally everywhere and only consumes about 20 mA."}}),_c('Desktop-Picture',{attrs:{"caption":"The Arduino Nano 33 BLE has an integrated 3 axis acceleration sensor, a microphone as well as an Bluetooth interface","width":"300px"}}),_c('Desktop-Text',{attrs:{"text":"The most common application of this technology is the recognition of wake words like Hey \"Siri\" or \"Alexa\" by known smart devices. \n    If those devices are in standby mode, a low powered chip is constantly waiting for you to say the wake word to power up the main CPU of the device."}}),_c('Desktop-Text',{attrs:{"text":"In December 2019, Peter Waren and Daniel Situnayake made this public by releasing a book, which explains machine learning with TensorFlow Lite on Arduino and Ultra-Low-Power Micro-Controllers. Since then, things got even easier with the launch of Edge Impulse: TinyML as a service to enable machine learning for all developers. It provides the full TinyML Pipeline, starting by collecting data, building a model and deploying it to the device. As a result, the implementation of motion- and sound recognition or finding anomalies in sensor data became more accessible"}}),_c('Desktop-Text',{attrs:{"text":"So I decided to build a voice recognition model with edge impulse!","size":"25px","weight":"650","align":"center"}}),_c('DesktopPictureSideText',{attrs:{"picture":_vm.lumos,"caption":"Because I am quite a big fan of Harry Potter, I thought it would be fun to build my own magic wand. If you are not into Harry Potter, let me tell you, that Lumos is the spell for creating light. On the other hand Nox makes the light disappear.","width":"250px"}}),_c('Desktop-Text',{attrs:{"text":"Data acquisition","size":"18px","weight":"550","align":"left"}}),_c('Desktop-Text',{attrs:{"text":"To get started with every machine learning project, you got to have some data in-store to train your model later on. Because I could not find datasets of people saying Lumos or Nox online, I had to sample the data myself. Thanks to Edge Impulse, this was not really a problem. After installing the Edge Impulse firmware on my Arduino and some further installations on my desktop PC I was able to record data and save it directly to my Edge Impulse project."}}),_c('Desktop-Picture',{attrs:{"caption":"Data acquisition in Edge Impulse","width":"800px","picture":_vm.data_acq}}),_c('Desktop-Text',{attrs:{"text":"So I took some time and recorded myself 12 minutes of data, which can be divided into 3 labels. Because I want my model to recognize me saying a spell name, I recorded myself ~4 minutes of saying Lumos as well asNox. Because the model also needs to recognize, what silence or noise looks like, I had to record ~4 min of noise as well."}}),_c('Desktop-Text',{attrs:{"text":"Building the model","size":"18px","weight":"550","a