 block in the impulse pipeline by changing some training parameters, I am pretty much done building my machine learning model. It is time to let the model train!"}}),_c('Desktop-Text',{attrs:{"text":"Training the model","size":"18px","weight":"550","align":"left"}}),_c('Desktop-Picture',{attrs:{"caption":"Training performance","width":"1000px","picture":_vm.result}}),_c('Desktop-Text',{attrs:{"text":"Sweet! Those results are looking pretty good. I was aware of the effect of overfitting my data but some live classification assured me, that the model is working not too shabby. In this article, I am not going over the process of live classification and retraining your model. I just want to mention, that this also works quite well."}}),_c('Desktop-Text',{attrs:{"text":"Deployment","size":"18px","weight":"550","align":"left"}}),_c('Desktop-Text',{attrs:{"text":"There are several possibilities to deploy your generated model to your device. One of them is creating a Arduino library in Edge Impulse and importing it in the Arduino IDE. This library comes with examples for testing your model, which makes it extremely easy to adjust the code for your specific use case."}}),_c('Desktop-Text',{attrs:{"text":"Result","size":"18px","weight":"550","align":"left"}}),_c('Desktop-Text',{attrs:{"text":"Like I said in the beginning, I wanted to light up something, when saying Lumos or shut the light, when saying Nox. I also wanted the microphone to only record, while a button is pressed. This is why I added some code and did some soldering on my Arduino. I really wanted this project to be fully embedded, so a battery block is attached at the back of my wand. So here it is:"}}),_c('Desktop-Picture',{attrs:{"caption":"poor mans wand","width":"400px","picture":_vm.wand}}),_c('Desktop-Text',{attrs:{"text":"Okay, it may not be as pretty and powerful as Dumbledores Elderwand, but it is quite functional. "}}),_c('Desktop-Text',{attrs:{"text":"Let me show you:"}}),_c('iframe',{attrs:{"id":"yout