  <div class="home">
    <div class="my_header">
      <DesktopHeader id="DesktopHeader"></DesktopHeader>
      <MobileHeader id="MobileHeader"></MobileHeader>
    </div>
    <div id="desktop-body">
      <DesktopEntry
        title="Identifying Antisemitic Tweets with Natural Language Processing in Python"
        subtitle="Let me show you a basic approach to classify text with machine learning, which won a competition at the Indiana University. This post is for everybody, who would like to get started with natural language processing (NLP) in Python."
        author_date="Jan Fiedler  Aug 18, 2020"
        :picture="certificate"
      ></DesktopEntry>
      <Desktop-Text
        text="Some weeks a good friend of mine, with a background in humanities and antisemitic studies, asked me if I would like to take part in an event at the Indiana University. The event was divided into a Datathon and Hackathon, a virtual workshop and competition with the goal to recognize antisemitism online and use programming to take care of this task automatically."
      >
      </Desktop-Text>
      <Desktop-Text
        text="You can read about the event in detail"
        linktext="HERE!"
        link="https://news.iu.edu/stories/2020/07/iub/24-antisemitism-workshop-twitter-competition-hackathon-datathon.html#"
      >
      </Desktop-Text>
      <Desktop-Text
        text="The use of social media in our modern society is huge and getting bigger by the day. With the increasing number of users, there is also a portion of people growing, who use these platforms as an outlet for homophobia, sexism, racism as well as antisemitism. Those hate speeches evolved from little internet trolls to a real pain to the digital society we are living in. The event aimed to fight against this particular threat and I was more than happy to contribute!"
      >
      </Desktop-Text>
      <Desktop-Text
        text="The Datathon was about manually annotating random tweets with keywords like Jews or Israel in it. For this task, a well-founded background in antisemitic studies was needed. The goal of the Hackathon, on the other hand, was to classify tweets programmatically as either antisemitic (1) or non-antisemitic (0). For the hackathon, a bunch of labeled data was given in a file. Because I was the only one with programming skills in the team, I was happy to think about a solution for the hackathon, even though I had no experience with NLP at all"
      >
      </Desktop-Text>
      <Desktop-Text
        text="4 weeks later"
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <div class="line">
        <hr>
      </div>
       <Desktop-Text
        text='"The first prize of $500 went to Jan Fiedler, Sydney Grad, Sophie Mariassy and Victor Tschiskale, students from Canada and Germany."'
        size="25px"
        weight="100"
        align="center"
      >
      </Desktop-Text>
      <div class="line">
        <hr>
      </div>
      <Desktop-Text
        text="We actually won!"
      >
      </Desktop-Text>
      <Desktop-Text
        text="The rest of my team annotated a crazy amount of tweets properly, which won us the datathon. For the hackathon, we also took first place because of my machine learning model, which achieved an F1 score of 0.90 on the final validation data."
      >
      </Desktop-Text>
      <Desktop-Text
        text='A basic NLP approach'
        size="20px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="In the following article, I want to reveal you a really basic approach for classifying text in Python. This approach is based on an article from Rebecca Vickery on"
        linktext="medium."
        link="https://medium.com/vickdata/detecting-hate-speech-in-tweets-natural-language-processing-in-python-for-beginners-4e591952223"
      >
      </Desktop-Text>
      <Desktop-Text
        text="Thank you so much for sharing!"
      >
      </Desktop-Text>
      <Desktop-Text
        text='The Data'
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="As I said, there was a data set given in a JSON file, consisting of labeled tweets either as antisemitic (1) or non-antisemitic (0). I converted the given JSON into a pandas data frame to keep the data science manner "
      >
      </Desktop-Text>
      <DesktopTable
      :items=table0
      caption="a part of the given data set"
      >
      </DesktopTable>
      <Desktop-Text
        text='Cleaning the Data'
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="In order to make any machine algorithm work well, I had to pre-process the given text data. As you can see, tweets contain characters like hashtags or emojis, which wont be meaningful to a machine learning algorithm."
      >
      </Desktop-Text>
      <Desktop-Text
        text="I am using the re library in Python for removing these characters."
      >
      </Desktop-Text>
      <Desktop-Code
      content=
      ' import re

      def f_clean_data(df,field):
          df[field]=df[field].str.lower()
          df[field] = df[field].apply(lambda elem: 
          re.sub(r"(@[A-Za-z0-9]+)|([^0-9A-Za-z \t])|(\w+:\/\/\S+)|^rt|http.+?","", elem))
          return df'
      >
      </Desktop-Code>
      <Desktop-Text
        text="This function also converts every word in lower case, which is nice because the following algorithm will count the frequency of words. Without this processing, a lower and upper version of the same word would count differently, even if the meaning is the same.
Lets have a look at the cleaned data."
      >
      </Desktop-Text>
      <DesktopTable
      :items=table1
      caption="a part of the given data set - cleaned"
      >
      </DesktopTable>
      <Desktop-Text
        text="This is looking a lot cleaner. After some research, I thought another important step of cleaning my tweets is needed: removing stop words and lemmatization. Stop words are the most common and basic words in a language like to, the or and. Those words had to be removed because they cant give a hint about the intention of a tweet. Lemmatization is a linguistic process of affiliating a word to its very basic form (lemma). For example runs, ran and running are forms of the same lemma: run. Like in the lowercase processing, this is really important to identify equal words even if they are in different forms."
      >
      </Desktop-Text>
      <Desktop-Text
        text="I implemented this by using spacy, a really strong library for NLP. I am basically splitting up my tweet as a text in separate words (tokens). If the token is a stop word, I dont want to append it to my cleaned text. If it is not a stop word, I want to apply the lemmatization and append it to my cleaned text."
      >
      </Desktop-Text>
      <Desktop-Code
      content=
      ' import spacy

      nlp=spacy.load("en_core_web_sm")

      def remove_stop_apply_lemma_for_string(text):
          cleaned_text=""
          tokens=nlp(text)
          for token in tokens:
              if not token.is_stop:
                  cleaned_text=cleaned_text+" "+token.lemma_
    
          return cleaned_text'
      >
      </Desktop-Code>
      <Desktop-Text
        text="At this point in pre-processing the tweets, it is getting harder to understand the text as a human being. On the other hand, this will improve the performance of our algorithm in the end."
      >
      </Desktop-Text>
      <DesktopTable
      :items=table2
      caption="a part of the given data set  applied lemmatization and removed stop words"
      >
      </DesktopTable>
      <Desktop-Text
        text='Balancing the Classes'
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="After having the data cleaned I realized, that the number of non-antisemitic tweets predominated the antisemitic tweets. If the machine learning model is only being fed with tweets, which are labeled as 0, it is more likely, that it will predict new tweets to 0 in the end.
Having the classes balanced is an important requirement to proceed in this stage."
      >
      </Desktop-Text>
      <Desktop-Text
        text="It is one approach, to either upsample or downsample the minority/majority of a class. At this point, it was really about trying which method scores better results. I went for upsampling the minority, where samples of the minority are being used multiple times until the class is the same size as the majority. For this implementation, I used the functions of the sklearn library."
      >
      </Desktop-Text>
      <Desktop-Code
      content=
      ' from sklearn.utils import resample
      import pandas as pd

      #they are like 1200 more antisemitic tweets than not antisemitic.
      #Thats why we are upsampling the minority.
      train_majority=clean_data[clean_data.Label=="0"]
      train_minority=clean_data[clean_data.Label=="1"]
    
      train_minority_upsampled=resample(train_minority,replace=True,
      n_samples=len(train_majority),random_state=123)

      train_upsampled=pd.concat([train_minority_upsampled,train_majority])'
      >
      </Desktop-Code>
      <Desktop-Text
        text='The Algorithm'
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="After having our whole data-set prepared, lets have a look at the chosen algorithm. A common approach to detect patterns in text is the use of the Bag of Words (BoW) model. A BoW model counts the frequency of single words and assigns a weight proportional to this frequency. I built this model with the help of the functions of the sklearn library."
      >
      </Desktop-Text>
      <Desktop-Text
        text='CountVectoriser'
        size="15px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="This function simply does the job of splitting a text into tokens and counting the frequency. It is possible to adjust some parameters on this function but I left it in default mode."
      >
      </Desktop-Text>
      <Desktop-Text
        text='TfidTransformer'
        size="15px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="This sklearn function called TfidfTransformer applies the frequency weighting to our machine learning model. Without this process of weighting tokens, less frequent words would vanish in the training process later on. To sum up: the TfidfTransformer assigns less value to more frequent words and allocates more value to less frequent but perhaps more meaningful words."
      >
      </Desktop-Text>
      <Desktop-Text
        text='Training the Model'
        size="18px"
        weight="550"
        align="left"
      >
      </Desktop-Text>
      <Desktop-Text
        text="For training my machine learning model I used a sklearn pipeline with a SGDClassifier. This pipeline object will apply every step of the BoW model to a given tweet."
      >
      </Desktop-Text>
      <Desktop-Text
        text="Before training the model, all the given data had to be split into a training and test set."
      >
      </Desktop-Text>
      <Desktop-Code
      content=
      "   from sklearn.feature_extraction.text import TfidfVectorizer
        from sklearn.pipeline import Pipeline
        from sklearn.feature_extraction.text import CountVectorizer
        from sklearn.feature_extraction.text import TfidfTransformer
        from sklearn.linear_model import SGDClassifier
        from sklearn.model_selection import train_test_split

        x_train,x_test,y_train,y_test=train_test_split(train_upsampled['Text'],
        train_upsampled['Label'],test_size=0.2)

        pipeline=Pipeline([('vect', CountVectorizer()),('tfidf', TfidfTransformer())
        ,('nb', SGDClassifier()),])"
      >
      </Desktop-Code>
      <Desktop-Text
        text="All it needs is to fit the training data into the pipeline and obtain a model. In the end, a prediction about the test data and the F1 score can be received."
      >
      </Desktop-Text>
      <Desktop-Code
      content=
      "   from sklearn.metrics import f1_score

        model=pipeline.fit(x_train,y_train)
        y_predict=model.predict(x_test)

        print(f1_score(y_test, y_predict))"
      >
      </Desktop-Code>
      <Desktop-Text
        text="This model achieved an F1 score of > 0.95 on the given test data and 0.90 on the final validation data. I was really surprised at how successful this rather basic approach was. I think this is a good foundation to get started with natural language processing but also has plenty of chances to improve."
      >
      </Desktop-Text>
      <Desktop-Text
        text="Check my GitHub for the full code!"
      >
      </Desktop-Text>
