<script>
// @ is an alias to /src

import DesktopHeader from "../components/Desktop/Article_Header.vue";
import DesktopEntry from "../components/Desktop/Article_entry.vue";
import DesktopText from "../components/Desktop/Article_Text.vue";
import DesktopCode from "../components/Desktop/Article_Code.vue";
//import DesktopPicture from "../components/Desktop/Article_Picture.vue";
//import DesktopPictureSideText from "../components/Desktop/Article_PictureSideText.vue";
import DesktopFooter from "../components/Desktop/Article_Footer.vue"
import DesktopTable from "../components/Desktop/Article_Table";

import MobileHeader from "../components/Mobile/Article_Header.vue";
import MobileEntry from "../components/Mobile/Article_entry.vue";
import MobileText from "../components/Mobile/Article_Text.vue";
import MobileCode from "../components/Mobile/Article_Code.vue";
import MobileTable from "../components/Mobile/Article_Table";
//import MobilePicture from "../components/Mobile/Article_Picture.vue";
//import MobilePictureSideText from "../components/Mobile/Article_PictureSideText.vue";
import MobileFooter from "../components/Mobile/Article_Footer.vue";

export default {
  name: "Home",
  components: {
    DesktopHeader,
    MobileHeader,
    DesktopEntry,
    DesktopText,
    DesktopCode,
    //DesktopPicture,
    //DesktopPictureSideText,
    DesktopFooter,
    MobileEntry,
    MobileText,
    MobileCode,
    MobileTable,
    //MobilePicture,
    //MobilePictureSideText,
    MobileFooter,
    DesktopTable
  },
  data() {
    return {
