<template>
  <div class="table">
    <b-table id="maintable" hover :fields="fields" :items="items"></b-table>
    <div class="Ccaption">
        <p>{{caption}}
      </p>
      </div>
  </div>
</template>

<script>
  export default {
    props:{
      items:[],
      caption:{
    type:String,
    default:"text text text"
  },
    },
    data() {
      return {
          fields: [
          {
            key: 'Nr',
            sortable: true,
            variant:'light'
          },
          {
            key: 'Label',
            sortable: true,
            variant:'light'
          },
          {
            key: 'Text',
            label: 'Text',
            sortable: false,
            // Variant applies to the whole column, including the header and footer
            variant: 'light'
          }
          ],
      }
    }
  }
</script>

